.featured{
    height: 100vh ;
}

.featured--vertical{
    height: inherit;
    width: inherit;
    background: linear-gradient(to top, #111 10%, transparent 90%);
}
.featured-horizontal{
    height: inherit;
    width: inherit;
    background: linear-gradient(to right, #111 30%, transparent 70%);
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 30px;
    padding-bottom: 80px;
}
.featured--name{
    font-size: 60px;
    font-weight: bold;
}
.featured--info{
    font-size: 18px;
    font-weight: bold;
    margin-top: 13px;
}
.featured--points,
.featured--year,
.featured--seasons{
    display: inline-block;
    margin-right: 15px;
}
.featured--points{
    color: #46d369;

}
.featured--description{
    max-width: 40%;
    margin-top: 15px;
    color: #999;
    font-size: 20px;
}
.featured--buttons{
    margin-top: 15px;
}
.featured--watchbutton,
.featured--mylistbutton{
    display: inline-block;
    font-size: 20px;
    font-weight: bold;
    padding: 12px 25px;
    text-decoration: none;
    margin-right: 15px;
    border-radius: 5px;
    /* margin-bottom: 10px; */
    opacity: 1;
}
.featured--watchbutton{
    background: #ffff;
    color: #000
}
.featured--mylistbutton{
    background: #333;
    color: #fff;
}
.featured--mylistbutton:hover, 
.featured--watchbutton:hover{
    opacity: .7;
    transition: all ease-in 0.2s;
}
.featured--genres{
    margin-top: 15px;
    font-size: 18px;
    color: #999
}


