@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@900&family=Open+Sans:ital,wght@0,300;0,400;0,500;0,600;0,700;0,800;1,300;1,400;1,500;1,600;1,700;1,800&family=Roboto:wght@400;700&display=swap');

*{
    box-sizing: border-box;
}
body{
    background-color: #111;
    color: #FFFF;
    margin:0;
    font-family: 'Roboto', 'sans-serif';
}
.lists{
    margin-top: -150px;
}
footer{
    margin: 30px 0;
    text-align: center;
}
footer a{
    text-decoration: none;
    color: #FFFF;
}
.loading{
    position:fixed;
    left: 0;
    top:0;
    right: 0;
    bottom: 0;
    z-index: 99;
    background: #000;
    display: flex;
    justify-content: center;
    align-items: center;
}